

define('game/levels/_base/main',['underscore', 'backbone', 'composite_views/perch', 'ui_widgets/proceed', 'entities/user_event'], function(_, Backbone, perch, proceed, UserEvent) {
  var View, _EVENTS, _me, _viewNameToModuleName;
  _me = 'game/levels/_base';
  _EVENTS = {
    start: 'test_started',
    interact: 'interacted',
    change: 'changed',
    readyToProceed: 'ready_to_proceed',
    notReadyToProceed: 'not_ready_to_proceed',
    end: 'test_completed'
  };
  _viewNameToModuleName = {
    ImageRank: 'image_rank',
    CirclesTest: 'circles_test',
    ReactionTime: 'reaction_time',
    Survey: 'survey'
  };
  View = Backbone.View.extend({
    initialize: function() {
      if (!((this.options.assessment != null) && (this.options.stageNo != null))) {
        throw new Error("Need assessment and stageNo");
      }
      if (this.model.attributes.data != null) {
        this.collection = new Backbone.Collection(this.model.attributes.data);
      }
      if (typeof this.start === "function") {
        return this.start();
      } else {
        return this.track(_EVENTS.start);
      }
    },
    _close: function() {
      proceed.hide();
      if (this.finalEventData != null) {
        this.track(_EVENTS.end, this.finalEventData);
      } else {
        this.track(_EVENTS.end);
      }
      this.trigger('done');
      if (typeof this.close === "function") {
        this.close();
      }
      this.remove();
      return this.options.assessment.nextStage();
    },
    checkAllInteracted: function(collection) {
      var interacted;
      interacted = collection.filter(function(item) {
        return item.get('interacted');
      });
      if (interacted.length === collection.length) {
        return true;
      }
    },
    readyToProceed: function() {
      if (this.alreadyReady) {
        return;
      }
      this.alreadyReady = true;
      proceed.show();
      this.listenToOnce(proceed, 'click', this._close);
      return this.track(_EVENTS.readyToProceed);
    },
    notReadyToProceed: function() {
      if (!this.alreadyReady) {
        return;
      }
      this.alreadyReady = false;
      proceed.hide();
      this.stopListening(proceed);
      return this.track(_EVENTS.notReadyToProceed);
    },
    track: function(eventName, event) {
      var baseData, userEvent;
      baseData = {
        event_desc: eventName,
        game_id: this.options.assessment.attributes.id,
        module: _viewNameToModuleName[this.model.attributes.view_name],
        stage: this.options.stageNo
      };
      userEvent = new UserEvent(_.extend(baseData, event));
      return userEvent.save();
    },
    remove: function() {
      proceed.hide();
      this.$el.remove();
      this.stopListening();
      return this;
    }
  });
  View.EVENTS = _EVENTS;
  return View;
});

define('game/levels/_base', ['game/levels/_base/main'], function (main) { return main; });
