

define('game/levels/circle_proximity/main',['underscore', 'backbone', 'entities/circles', 'text!./instructions.hbs', 'composite_views/perch', './self_view', './proxy_view', 'ui_widgets/proceed'], function(_, Backbone, Circles, instructions, perch, SelfView, Proxy, proceed) {
  var View, _USEREVENTS, _degToRad, _me;
  _me = 'game/levels/circle_proximity';
  _USEREVENTS = {
    dragStart: '',
    dragStop: ''
  };
  _degToRad = function(deg) {
    return deg * Math.PI / 180;
  };
  View = Backbone.View.extend({
    className: 'circleProximity',
    initialize: function() {
      this.listenTo(this.collection, 'change:userChangedPos', this.onChangeUserChangedPos);
      this.listenToOnce(proceed, 'click', this._close);
      return _.bindAll(this, '_renderLevel');
    },
    render: function() {
      if (this.options.showInstructions) {
        return perch.show({
          content: instructions,
          btn1Text: "I'm Ready",
          large: true,
          onClose: this._renderLevel,
          mustUseButton: true,
          supressTracking: true
        });
      } else {
        return this._renderLevel();
      }
    },
    _renderLevel: function() {
      var radius,
        _this = this;
      this.selfView = new SelfView;
      this.$el.html(this.selfView.render().el);
      this.selfView.position();
      this.$line = this.$('.line');
      radius = this.selfView.$el.width() / 2;
      this.$line.css({
        left: radius,
        top: radius
      });
      this.collection.each(function(circle) {
        circle.view = new Proxy({
          model: circle,
          $line: _this.$line,
          selfView: _this.selfView,
          track: _.bind(_this.options.runner.track, _this.options.runner)
        });
        return _this.$el.append(circle.view.render().el);
      });
      this._positionCirclesAround(this.selfView.getSelfCenter());
      return this;
    },
    _positionCirclesAround: function(center) {
      var count, deg, distance, increment,
        _this = this;
      count = this.collection.length;
      increment = 360 / count;
      deg = increment / 2 + 270;
      distance = 250;
      return this.collection.each(function(circle) {
        circle.view.positionByCenter({
          x: Math.cos(_degToRad(deg)) * distance + center.x,
          y: Math.sin(_degToRad(deg)) * distance + center.y
        });
        return deg += increment;
      });
    },
    _checkDone: function() {
      var changedCircles;
      changedCircles = this.collection.filter(function(circle) {
        return circle.get('userChangedPos');
      });
      if (changedCircles.length === this.collection.length) {
        return true;
      }
    },
    _close: function() {
      this.collection.each(function(circle) {
        return circle.view.remove();
      });
      proceed.hide();
      this.trigger('done');
      return this.remove();
    },
    onChangeUserChangedPos: function() {
      if (this._checkDone()) {
        return proceed.show();
      }
    }
  });
  return View;
});

define('game/levels/circle_proximity', ['game/levels/circle_proximity/main'], function (main) { return main; });
