

define('dashboard/widgets/base/main',['backbone', 'Handlebars', 'text!./loading.hbs', 'text!./base.hbs'], function(Backbone, Handlebars, tmplLoading, tmplBase) {
  var View, _className, _me;
  _me = 'dashboard/widget';
  _className = 'holder';
  View = Backbone.View.extend({
    className: _className,
    tagName: 'section',
    tmplBase: Handlebars.compile(tmplBase),
    initialize: function() {
      if (this.model) {
        this.renderLoading();
        this.listenTo(this.model, 'sync', this.render);
      } else {
        this.render();
      }
      return this.$el.addClass(_className);
    },
    renderLoading: function() {
      this.$el.html(tmplLoading);
      return this;
    }
  });
  return View;
});

define('dashboard/widgets/base', ['dashboard/widgets/base/main'], function (main) { return main; });
