

define('game/levels/alex_trebek/main',['underscore', 'backbone', 'game/levels/_base', 'ui_widgets/icon_slider'], function(_, Backbone, Level, IconSlider) {
  var View, _me;
  _me = 'game/levels/alex_trebek';
  View = Level.extend({
    className: 'alexTrebek',
    render: function() {
      var _this = this;
      console.log("" + _me + ".render()");
      this.$el.empty();
      if (this.collection != null) {
        this.collection.each(function(question) {
          var slippy;
          slippy = new IconSlider({
            model: question
          });
          _this.$el.append(slippy.render().el);
          _this.listenTo(slippy, 'slide', _this.onSlide);
          return _this.listenTo(slippy, 'stop', _this.onStop);
        });
      }
      return this;
    },
    onSlide: function(data) {
      return this.track(Level.EVENTS.interact, {
        item_id: data.model.attributes.item_id,
        item_topic: data.model.attributes.item_topic,
        item_value: data.value
      });
    },
    onStop: function(data) {
      data.model.set({
        'interacted': true
      });
      this.track(Level.EVENTS.change, {
        item_id: data.model.attributes.item_id,
        item_topic: data.model.attributes.item_topic,
        item_value: data.value
      });
      if (this.checkAllInteracted(this.collection)) {
        return this.readyToProceed();
      }
    }
  });
  return View;
});

define('game/levels/alex_trebek', ['game/levels/alex_trebek/main'], function (main) { return main; });
