

define('entities/user_event',['underscore', 'backbone', 'core'], function(_, Backbone, app) {
  var UserEvent, _me;
  _me = 'entities/user_event';
  UserEvent = Backbone.Model.extend({
    urlRoot: "" + app.cfg.apiServer + "/api/v1/user_events",
    defaults: function() {
      return {
        game_id: null,
        module: null,
        stage: null,
        record_time: new Date().getTime(),
        event_desc: 'Default Event Description'
      };
    },
    initialize: function() {
      this.on('sync', this.onSync);
      return this.on('error', this.onErr);
    },
    onSync: function(model) {},
    onErr: function() {
      return console.error("" + _me + " error event");
    }
  });
  return UserEvent;
});
