

define('core/router',['jquery', 'backbone'], function($, Backbone) {
  var MainRouter, _me, _welcomePageUrlPrefix;
  _me = 'routers/main_router';
  _welcomePageUrlPrefix = '/welcome/';
  MainRouter = Backbone.Router.extend({
    routes: {
      '': 'showHome',
      home: 'showHome',
      about: 'showAbout',
      team: 'showTeam',
      startGame: 'showDemographics',
      demographics: 'showDemographics',
      'game/:def_id': 'createGame',
      game: 'createDefaultGame',
      guestSignup: 'showGuestSignup',
      dashboard: 'showDashboard',
      'dashboard-career': 'showDashCareer',
      'referrer/:refId': 'recordReferrer'
    },
    initialize: function(appCoreSingleton) {
      return this.app = appCoreSingleton;
    },
    showHome: function() {
      if (this.app.user.attributes.referred_by != null) {
        return window.location.href = _welcomePageUrlPrefix + this.app.user.attributes.referred_by;
      } else {
        return this.app.view.asSite('pages/home');
      }
    },
    showAbout: function() {
      return this.app.view.asSite('pages/about');
    },
    showTeam: function() {
      return this.app.view.asSite('pages/team');
    },
    showDemographics: function() {
      return this.app.view.asGame('pages/demographics');
    },
    createDefaultGame: function() {
      return this.createGame('baseline');
    },
    createGame: function(def_id) {
      return this.app.view.asGame('pages/play_game', {
        def_id: def_id
      });
    },
    showGuestSignup: function() {
      return this.app.view.asGame('pages/guest_signup');
    },
    showDashboard: function() {
      return this.app.view.asDash('pages/dashboard/all');
    },
    showDashCareer: function() {
      return this.app.view.asDash('pages/dashboard/career');
    },
    recordReferrer: function(refId) {
      this.app.analytics.track('Referral', refId);
      this.app.user.set({
        referred_by: refId
      });
      this.showDemographics();
      return this.navigate('startGame', {
        replace: true
      });
    }
  });
  return MainRouter;
});
