

define('charts/chart',['underscore', 'backbone', 'Handlebars', './chart_colors'], function(_, Backbone, Handlebars, chartColors) {
  var Model;
  Model = Backbone.Model.extend({
    defaults: {
      name: 'Default Chart Name',
      width: 150,
      height: 150,
      chartValues: [],
      colors: _.clone(chartColors),
      options: {}
    },
    _prepareData: function(data) {
      var i, label, preppedData, value;
      preppedData = [];
      i = 0;
      for (label in data) {
        value = data[label];
        preppedData.push({
          label: label,
          color: chartColors[i],
          value: value
        });
        i++;
      }
      return preppedData;
    },
    parse: function(resp) {
      resp.chartValues = this._prepareData(resp.chartValues);
      return resp;
    }
  });
  return Model;
});
