

define('pages/dashboard/career',['backbone', 'Handlebars', 'core', 'dashboard/widgetmaster'], function(Backbone, Handlebars, app, Widgetmaster) {
  var View;
  View = Backbone.View.extend({
    className: 'dashboard-career',
    initialize: function() {
      this.listenTo(app.user, 'sync', this.render);
      if (!app.user.isLoggedIn()) {
        return app.trigger('session:showLogin');
      }
    },
    render: function() {
      this.widgetmaster = new Widgetmaster({
        widgets: ['dashboard/career/jobs', 'dashboard/career/skills', 'dashboard/career/tools']
      });
      this.$el.html(this.widgetmaster.render().el);
      return this;
    }
  });
  return View;
});
